import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import ValidasiMasterGudang from "../../../Validasi/ValidasiMasterGudang.jsx";

const maptostate = (state) => {
  if (state.datamaster.ShowModalGudang !== null) {
    return {
      initialValues: {
        kode_gudang: state.datamaster.ShowModalGudang.kode_gudang,
        nama_gudang: state.datamaster.ShowModalGudang.nama_gudang,
      },
    };
  }
};
class FormModalMasterGudang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listgroup: [],
      aktif: false,
    };
  }
  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit
      ? document.getElementById("nama_gudang").focus()
      : document.getElementById("kode_gudang").focus()
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <Field
          id="kode_gudang"
          tabIndex="1"
          name="kode_gudang"
          component={ReanderField}
          type="text"
          label="Kode Gudang"
          readOnly={this.props.isEdit}
          placeholder="Masukan Kode Gudang"
        />
        <Field
          tabIndex="2"
          id="nama_gudang"
          name="nama_gudang"
          component={ReanderField}
          type="text"
          label="Nama Gudang"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Nama Gudang"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormModalMasterGudang = reduxForm({
  form: "ModalMasterGudang",
  enableReinitialize: true,
  validate: ValidasiMasterGudang,
})(FormModalMasterGudang);
export default connect(maptostate, null)(FormModalMasterGudang);
